.react-calendar {
  margin: 0 auto;
  width: 100%;
  height: max-content;
  display: grid;
  gap: 5%;
}

.react-calendar__navigation {
  display: grid;
  grid-template-columns: 0.01fr 0.01fr 0.3fr 0.01fr 0.01fr;
  height: 2em;
  justify-content: center;
}

.react-calendar__navigation button {
  text-align: center;
  border: 3px solid hsla(0, 0%, 100%, 0.6);
  color: hsla(0, 0%, 100%, 0.6);
  background-color: transparent !important;
  background-image: none !important;
  font-size: 1em;
  font-family: "Hurricane", "Yeon Sung", cursive;
}

.react-calendar__navigation__prev-button,
.react-calendar__navigation__prev2-button,
.react-calendar__navigation__next2-button,
.react-calendar__navigation__next-button {
  font-family: "Courier New", Courier, monospace;
  font-size: 1.2em;
}

.react-calendar__navigation__prev2-button {
  border-radius: 10px 0 0 10px;
}
.react-calendar__navigation__next2-button {
  border-radius: 0 10px 10px 0;
}

.react-calendar__month-view__weekdays {
  height: 50px;
  font-size: 20px;
  text-align: center;
  align-items: center;
}

.react-calendar__month-view__days {
  display: grid !important;
  grid-template-columns: 0.14fr 0.14fr 0.14fr 0.14fr 0.14fr 0.14fr 0.14fr;
  align-content: flex-start;
  align-items: stretch;
  justify-content: space-evenly;
  height: min-content;
}

.react-calendar__viewContainer > div > div {
  width: 90%;
  margin: 0 auto;
}

button.react-calendar__month-view__days__day--neighboringMonth {
  border: 3px solid hsla(0, 4%, 46%, 0.6) !important;
  color: hsla(0, 4%, 46%, 0.6) !important;
}

.react-calendar__year-view__months {
  display: grid !important;
  grid-template-columns: 0.5fr 0.5fr 0.5fr 0.5fr;
  align-content: stretch;
  align-items: stretch;
  justify-content: center;
}

.react-calendar__decade-view__years,
.react-calendar__century-view__decades {
  display: grid !important;
  grid-template-columns: 0.5fr 0.5fr 0.5fr 0.5fr 0.5fr;
  align-content: stretch;
  align-items: stretch;
  justify-content: center;
}

.react-calendar__month-view__days button,
.react-calendar__year-view__months button,
.react-calendar__decade-view__years button,
.react-calendar__century-view__decades button {
  border-radius: 20px;
  font-family: "Jeju Hallasan", cursive;
  height: 80px;
  font-size: 24px;
  border: 3px solid hsla(0, 0%, 100%, 0.6);
  color: hsla(0, 0%, 100%, 0.6);
  background-color: transparent !important;
  background-image: none !important;
  margin: 0 0 5px 5px;
}

.react-calendar__month-view__days__day button {
  position: relative;
}

.react-calendar__month-view__days__day abbr[aria-label] {
  position: absolute;
}

.react-calendar__month-view__days > button > abbr {
  position: absolute;
  transform: translate(2px, -12px);
}

.react-calendar__viewContainer {
  height: 49vh;
}

.react-calendar__month-view__days button {
  text-align: center;
  padding: 0px 36px 37px 0px;
  height: 70px;
  width: 70px;
}

.react-calendar__tile--now
  > abbr:not(.react-calendar__year-view__months__month > abbr) {
  background-color: red;
  width: 30px;
  height: 30px;
  border-radius: 100%;
}

.hot-dot {
  position: absolute;
  height: 0;
  width: 0;
  border-bottom: 7px solid #f87171;
  border-top: 6px solid transparent;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  transform: translate(5px, 25px);
}

.nomal-dot {
  position: absolute;
  height: 8px;
  width: 8px;
  background-color: #ced101;
  border-radius: 50%;
  transform: translate(20px, 30px);
}

.no-dot {
  display: none;
}

@media screen and (max-width: 1080px) {
  .react-calendar {
    justify-content: center;
  }
}

@media screen and (max-width: 560px) {
  .react-calendar__month-view__days > button > abbr {
    position: absolute;
    transform: translate(2px, 1px);
    font-size: 0.8em;
  }
  .react-calendar__month-view__days button {
    height: 50px;
    width: 50px;
  }

  .hot-dot {
    transform: translate(5px, 20px);
  }
  .nomal-dot {
    transform: translate(20px, 25px);
  }
  .react-calendar__tile--now
    > abbr:not(.react-calendar__year-view__months__month > abbr) {
    width: 20px;
    height: 20px;
  }
}
